import React from 'react';
import FooterMain from '../Footer/FooterMain';
import FooterSm from '../Footer/FooterSm';
import RedRustRogImg from './Assets/RedRust.jpg';
// blog with image on the left side and text on the right side - start
const RedRustRog = () => {
    return (
        <div>
            <main className='pt-8 pb-16 lg:pt-16 lg:pb-24 bg-white antialiased font-sans'>
                <div className='flex justify-between px-4 mx-auto max-w-screen-xl'>
                    <article className='mx-auto w-full max-w-2xl format format-sm sm:format-base lg:format-lg format-blue '>
                        <header className='mb-4 lg:mb-6 not-format'>
                            <address className='flex items-center mb-6 not-italic'>
                                <div className='inline-flex items-center mr-3 text-sm text-gray-900 '>
                                    <img class="mr-4 w-16 h-16 rounded-full" src="https://www.svgrepo.com/show/65676/avatar.svg" alt="Syeda Sirjum Munira" />
                                    <div>
                                        <p rel="author" class="text-xl font-bold text-gray-900">Syeda Sirjum Munira</p>
                                        <p class="text-base font-light text-gray-500"><time pubdate datetime="2022-12-04" >Dec. 4, 2022</time></p>
                                    </div>
                                </div>
                            </address>
                            <h1 class="mb-4 text-3xl font-extrabold leading-tight text-gray-900 lg:mb-6 lg:text-4xl ">    (Red Rust) </h1>
                        </header>
                        <p class="lead mb-2">    (Red rust)    (Cephaleuros virescens)    (Cephaleuros parasiticus)                                           </p>

                        <figure className='text-center italic'>
                            <img src={RedRustRogImg} className='rounded-md' alt="" />
                            <figcaption className='text-sm'>    </figcaption>
                        </figure>

                        <h3 className='mt-2 mb-1 text-xl font-bold leading-tight text-gray-900 lg:mb-6 lg:text-2xl '></h3>
                        <ul className='list-disc list-inside mb-6'>
                            <li className='mb-1'>Cephaleuros virescens  Cephaleuros paraciticus         ,       </li>
                            <li className='mb-1'>       |</li>
                            <li className='mb-1'>              </li>
                            <li className='mb-1'>     </li>
                            <li className='mb-1'>        -  </li>
                        </ul>
                        <h3 className='mt-2 mb-1 text-xl font-bold leading-tight text-gray-900 lg:mb-6 lg:text-2xl '></h3>
                        <ul className='list-disc list-inside mb-6'>
                            <li className='mb-1'>        </li>
                            <li className='mb-1'>    (-  )        -   -    </li>
                            <li className='mb-1'>       </li>

                        </ul>
                        <p className='mt-2 text-md leading-tight text-gray-600 lg:text-2xl '>        <span className='text-blue-500'>
                             
                        </span></p>

                    </article>

                </div>

            </main>
            <FooterMain />
            <FooterSm />
        </div>
    );
};

export default RedRustRog;